@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alegreya&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
@import url(http://fonts.cdnfonts.com/css/vogue);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.tech__wrapper,
.techstack__title {
  display: flex;
  margin-bottom: 10px;
}

.tech__wrapper {
  justify-content: space-between;
  padding: 2em;
  border-bottom: 2px solid black;
  margin-bottom: 3em;
}

.tech__logos {
  flex-direction: column;
}

.tech__text {
  color: #2368a2;
  font-weight: 650;
}


.tech__logos img {
  width: 60px;
}

.homepage__location {
  color: #12283a;
  font-size: 14px;
  font-weight: 800;
  font-family: 'Titillium Web', sans-serif;
}


.homepage__name__wrapper {
  display: flex;
  margin: 1em;
  flex-direction: row;
}

.homepage__wrapper {
  margin: 5% 0 3% 0;
  display: flex;
  left: 85px;
  justify-content: center;
}

.homepage__title {
  color: #2368a2;
  font-size: 2em;
  font-family: 'Alegreya', serif;
}

.homepage__name,
.homepage__surname {
  font-family: 'Vogue', sans-serif;
  font-weight: 800;
  font-size: 8em;
}

.homepage__surname {
  color: #2368a2;
}

.portrait {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  width: 300px;
  margin-right: 2em;
}

.portrait img {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.footer__author {
  color: #2368a2;
}

.homepage__contact,
.homepage__hotlinks {
  display: flex;
  justify-content: center;
}

.homepage__hotlinks img {
  max-width: 200px;
  margin: 20px;
}

@media (max-width: 500px) {
  .homepage__name__wrapper {
    flex-direction: column;
  }

  .homepage__location {
    text-align: center;
  }

  .homepage__info__wrapper {
    margin-top: 0;
  }

  .portrait {
    margin: 0;
  }

  .homepage__hotlinks img {
    max-width: 100px;
  }

  .homepage__location {
    text-align: center;
  }
}

@media (max-width: 400px) {
  .homepage__name,
  .homepage__surname {
    font-size: 7em;
  }
}

.homepage__work__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.homepage__work__wrapper img {
  width: 64px;
}

.portfolio__header {
  display: flex;
}

.portfolio__header {
  justify-content: center;
  font-size: 2em;
}

.portfolio__experience {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 5em 5em 5em;
}

.portfolio__experience--category {
  text-align: center;
  color: #2368a2;
}

.portfolio__experience__card,
.portfolio__education,
.portfolio__work {
  background-color: #eff8ff;
  border: solid 2px #bdb7b7;
  padding: 1em;
  margin: 1em;
  text-align: center;
  min-width: 350px;
}

.portfolio__education,
.porfolio__work {
  flex-basis: 50%;
}

.portfolio__experience--title,
.portfolio__wrapper--title {
  font-family: 'Vogue', sans-serif;
  font-weight: 800;
  text-align: center;
  margin-top: 1em;
}

.cv__wrapper {
  justify-content: space-between;
}

.portfolio__wrapper {
  display: flex;
  justify-content: center;
}

.portfolio__page {
  margin-bottom: 15%;
}

.portfolio__work,
.portfolio__education {
  margin-top: 2em;
  background-color: #eff8ff;
  border: solid 2px #bdb7b7;
}
.portfolio__work p,
.portfolio__education p {
  font-size: 17px;
}

.portfolio__experience--category__wrapper {
  display: flex;
  justify-content: space-between;
}

.iconify {
  width: 50px;
}

.portfolio__education img {
  display: flex;
  max-width: 100%;
}

@media (max-width: 1500px) {
  .portfolio__education img {
    width: 1000px;
  }
}

@media (max-width: 1300px) {
  .portfolio__education img {
    width: 800px;
  }
}

@media (max-width: 1200px) {
  .portfolio__experience,
  .portfolio__wrapper {
    flex-wrap: wrap;
  }

  .portfolio__education,
  .portfolio__work {
    flex-basis: 70%;
  }

  .portfolio__experience {
    margin: 0;
  }
}

@media (max-width: 1000px) {
  .portfolio__education img {
    width: 600px;
  }
}

@media (max-width: 700px) {
  .portfolio__education img {
    width: 400px;
  }
  .portfolio__work,
  .portfolio__education {
    flex-basis: 30%;
  }
}

@media (max-width: 500px) {
  .portfolio__work p,
  .portfolio__education p {
    font-size: 12px;
  }

  .portfolio__experience {
    margin: 0;
  }

  .portfolio__experience--category {
    font-size: 20px;
  }

  .portfolio__experience--title,
  .portfolio__wrapper--title {
    font-size: 20px;
    margin: 15px 0 0 8px;
  }

  .portfolio__experience,
  .portfolio__wrapper {
    flex-direction: column;
  }

  .portfolio__education img {
    width: 300px;
  }
}

.cv__wrapper {
  display: flex;
  color: white;
}

.cv__wrapper p {
  margin: 1em;
  color: #343a40;
}

.cv__date {
  font-weight: 650;
  color: #1a4971;
}

.project__wrapper,
.project__card {
  display: flex;
  justify-content: space-evenly;
}

.project__card {
  border: solid 1px white;
  color: #2368a2;
  border: solid 2px #bdb7b7;
  background-color: #ffffff;
}

.project__text {
  font-size: 13pt;
  text-align: center;
}

.project__topic {
  color: rgb(134, 121, 255);
}

.project__title {
  text-align: center;
  font-size: 1.4em;
}

.project__wrapper {
  margin-top: 1%;
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 10em;
}

.project__image {
  object-fit: cover;
  height: 250px;
}

.project__card {
  margin: 1em;
}

.button__wrapper {
  margin: 1em;
  display: flex;
  justify-content: space-evenly;
}

.project__title {
  text-align: center;
  color: #63a2d8;
  margin: 1em;
  font-family: 'Titillium Web', sans-serif;
  font-weight: 800;
}

.link__button {
  margin-bottom: 1em;
}

.btn:hover {
  color: green;
}

.card-text {
  padding: 5px;
}

@media (max-width: 1200px) {
  .project__card {
    flex-basis: 65%;
  }
}

@media (max-width: 500px) {
  .project__wrapper {
    flex-direction: column;
    align-items: center;
  }

  .project__card {
    margin: 1em;
  }

  .project__image {
    height: 300px;
  }

  .project__title {
    font-size: 20px;
  }
}

